import Particles from "react-tsparticles";

export const Backdrop = () => {
    return(
<Particles width='100vw' height="100vh"  id="tsparticles"
        options={{
            "background": {
              "color": {
                "value": "#fff"
              },
              "position": "50% 50%",
              "repeat": "no-repeat",
              "size": "cover",
              "opacity": 0,
            },
            "fpsLimit": 60,
            "interactivity": {
              "events": {
                "onClick": {
                  "mode": "push"
                },
                "onHover": {
                  "mode": "repulse",
                  "parallax": {
                    "force": 60
                  }
                }
              },
              "modes": {
                "bubble": {
                  "distance": 400,
                  "duration": 2,
                  "opacity": 0.8,
                  "size": 40
                },
                "grab": {
                  "distance": 400
                }
              }
            },
            "particles": {
              "color": {
                "value": "#000"
              },
              "links": {
                "color": {
                  "value": "#000"
                },
                "distance": 150,
                "enable": true,
                "opacity": 0.4
              },
              "move": {
                "attract": {
                  "rotate": {
                    "x": 600,
                    "y": 1200
                  }
                },
                "enable": true,
                "outModes": {
                  "bottom": "out",
                  "left": "out",
                  "right": "out",
                  "top": "out"
                }
              },
              "number": {
                "value": 0
              },
              "opacity": {
                "value": 0.5,
                "animation": {
                  "minimumValue": 0.1,
                  "speed": 1
                }
              },
              "shape": {
                "options": {
                  "polygon": {
                    "nb_sides": 5
                  },
                  "star": {
                    "nb_sides": 5
                  },
                  "image": {
                    "src": "https://cdn.matteobruni.it/images/particles/github.svg",
                    "width": 100,
                    "height": 100
                  },
                  "images": {
                    "src": "https://cdn.matteobruni.it/images/particles/github.svg",
                    "width": 100,
                    "height": 100
                  }
                }
              },
              "size": {
                "random": {
                  "enable": true
                },
                "value": 5,
                "animation": {
                  "minimumValue": 0.1,
                  "speed": 40
                }
              },
              "stroke": {
                "color": {
                  "value": "#000000",
                  "animation": {
                    "enable": false,
                    "speed": 1,
                    "sync": true
                  }
                }
              }
            },
            "emitters": [
              {
                "direction": "top-right",
                "life": {},
                "rate": {
                  "quantity": 1,
                  "delay": 0.1
                },
                "particles": {
                  "shape": {
                    "type": "circle"
                  },
                  "color": {
                    "value": "random"
                  },
                  "lineLinked": {
                    "enable": false
                  },
                  "opacity": {
                    "value": 0.3
                  },
                  "rotate": {
                    "value": 0,
                    "random": true,
                    "direction": "counter-clockwise",
                    "animation": {
                      "enable": true,
                      "speed": 15,
                      "sync": false
                    }
                  },
                  "size": {
                    "value": 10,
                    "random": {
                      "enable": true,
                      "minimumValue": 5
                    }
                  },
                  "move": {
                    "speed": 5,
                    "random": false,
                    "outMode": "bounce"
                  }
                },
                "position": {
                  "x": 0,
                  "y": 100
                }
              }
            ]
          }}/>
    );
}